* {
  padding: 0;
  margin: 0;
  box-sizing: content-box;
}
@-webkit-keyframes appear{
  from{opacity: 0;}
  to{opacity: 100%;}
}
@keyframes appear{
  from{opacity: 0;}
  to{opacity: 100%;}
}
.App{
  -webkit-animation-name: appear;
          animation-name: appear;
  -webkit-animation-duration: 3s;
          animation-duration: 3s
}/*  p -->font*//*  h2 -->font*/
.extra-margin {
  text-align: center;
  padding: 1% 10%;
}
a {
  color: inherit;
  text-decoration: none;
}

h1 {
  font-family: 'Parisienne', cursive;
  font-size: 3rem;
}
h2 {
  font-family: 'Overpass', sans-serif;
}
h3 {
  margin: 2% 0;
}
p {
  font-family: 'Varela Round', sans-serif;
  margin: 2% 0;
}
i {
  margin: 0 10px;
}
.Header-parent {
  padding: 2% 1%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 2px solid black;
}
.heads {
  display: block;
  -webkit-align-self: center;
          align-self: center;
  -webkit-flex-grow: 2;
          flex-grow: 2;
  -webkit-flex-basis: 400px;
          flex-basis: 400px;
}
.social-handle {
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: center;
          align-self: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}
.login-status {
  transition-duration: 0.4;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.login {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-align-self: center;
          align-self: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.button-login {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.signin {
  color: black;
  background-color: white;
  border: 2px solid black;
  width: 100px;
  height: auto;
  margin: 0 2%;
  transition-duration: 0.4s;
  padding: 9px 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}
.signin:hover {
  background-color: rgb(51, 49, 49); /* Green */
  color: white;
}

/* TRY     -       --------- */
.showCase {
  margin: 3% 1%;
  overflow: hidden;

  display: -webkit-flex;

  display: flex;
  position: relative;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.left-arrow {
  z-index: 10;
  position: absolute;
  top: 45%;
  left: 10%;
  font-size: 3rem;
}
.right-arrow {
  z-index: 10;
  position: absolute;
  top: 45%;
  right: 10%;
  font-size: 3rem;
}
.showCase-cont {
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  overflow: inherit;
  display: inherit;
  max-height: 60vh;
  border-radius: 15px;
}
.image {
  border-radius: 15px;
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.trial-cont-heading {
  -webkit-animation-name: move;
          animation-name: move;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;

  -webkit-animation-timing-function: ease-in-out;

          animation-timing-function: ease-in-out;

  min-width: 500px;

  width: 50vw;
  position: relative;
  z-index: 1;
}
@-webkit-keyframes move {
  0% {
    -webkit-transform: translateX(-700px);
            transform: translateX(-700px);
  }
  25% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  75% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  100% {
    -webkit-transform: translateX(700px);
            transform: translateX(700px);
  }
}
@keyframes move {
  0% {
    -webkit-transform: translateX(-700px);
            transform: translateX(-700px);
  }
  25% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  75% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  100% {
    -webkit-transform: translateX(700px);
            transform: translateX(700px);
  }
}

.trial-cont-heading p {
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  position: absolute;
  text-align: center;
  top: 50%;
  left: 50%;
  color: azure;
  font-size: 2rem;
}

/* TRY     -       --------- */

.post-area-title {
  display: -webkit-flex;
  display: flex;
  margin: 1% 2%;
  -webkit-align-items: center;
          align-items: center;
}
.new-posts-title {
  display: -webkit-flex;
  display: flex;
}
.new-posts-title h2 {
  margin-right: 25px;
  -webkit-flex: 1 1;
          flex: 1 1;
}
.post-area-title span {
  -webkit-flex: 3 1;
          flex: 3 1;
  border: 1px solid black;
  height: 0px;
}
.various-posts {
