  color: white;
}
.footer-right h3 {
  margin: 2% 0;
}

.footer-main {
  margin: 5% 5%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.footer-left,
.footer-right {
