@media only screen and (max-width: 605px) {
  .footer-right,
  .footer-left {
    min-width: 150px;
    width: 50px;
  }
  .footer-right p {
    margin-bottom: 12px;
  }
  .social-icons {
    margin-top: 20px;
  }
  .extra-margin {
    padding: 15px;
  }
  .heads {
    margin-top: 25px;
    -webkit-order: 0;
            order: 0;
    width: 100%;
  }
  .login-status {
    width: 0%;
    display: -webkit-flex;
    display: flex;
  }
  .social-handle,
  .login-status {
    margin-top: 15px;
    margin-bottom: 15px;
    -webkit-order: 1;
            order: 1;
  }
  .Header-parent {
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
  .posts-container {
    width: 84vw;
  }
  .showCase-cont {
    margin: 20px 0;
  }
  .trial-cont-heading {
    width: 100vw;
    display: grid;
    -webkit-justify-content: center;
            justify-content: center;
  }
  .trial-cont-heading p {
    left: 50%;
    font-size: 30px;
    margin: 3px 3px;
  }
  .trial-cont-heading img {
    width: 90vw;
    height: 58vh;
  }
  .signin {
    width: 54px;
    padding: 6px 7px;
  }
  .home-icon {
    margin: 0 3%;
    margin-top: 10px;
  }
}

.singlepost-container {
