/*  COMMENT INPUT----------------------- */
input[type="text"] {
  font-size: 1rem;
  width: 70%;
  height: 50px;
  border: none;
  border-bottom: 2px solid black;
  margin-right: 5px;
}
input[type="text"]:focus {
  outline: none;
}
/* COMMENT INPUT ----------------------- */
/* COMMENT BUTTON  ----------------------- */
button[type="submit"] {
  width: 109px;
  height: 40px;
  background-color: rgb(51, 49, 49);
  border: 2px solid black;
  font-size: 16px;
  margin: 0 2%;
  color: white;
  transition-duration: 0.4s;
  padding: 6px 13px;
}
button[type="submit"]:hover {
  background-color: white; /* Green */
  color: black;
}
/* COMMENT BUTTON  ----------------------- */
/* COMMENTLIST  -------------------------- */
.singlecomment-container {
