  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  margin: 24px 4%;
  text-align: left;
  -webkit-animation-name: comment;
          animation-name: comment;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}
@-webkit-keyframes comment{
  from {width:0}
  to{width: 90%;}
}
@keyframes comment{
  from {width:0}
  to{width: 90%;}
}
.singlecomment-img {
  -webkit-flex: 1 1;
          flex: 1 1;
  margin-right: 25px;
}
.singlecomment-img img {
  border-radius: 50%;
}
.singlecomment-text {
